<template>
    <div class="fixed_div">
        <nav class='navbar'>
            <ul>
                <li id="home_li"><router-link to="/">Home</router-link></li> 
                <li id="about_li"><router-link to="/about">About</router-link></li> 
                <li id="tutorial_li"><router-link to="/tutorial">Tutorial</router-link></li> 
                <li id="statistics_li"><router-link to="/statistics">Statistics</router-link></li> 
                <li id="download_li"><router-link to="/download">Download</router-link></li> 
                <li id="faq_li"><router-link to="/faq">Faq</router-link></li> 
            </ul>  
        </nav>
        <div class="head_font">     
            <div class="head_font_name">GROBASE</div>
            <div class="head_font_date">2018</div>
        </div> 
    </div>    
</template>

<style scoped>
    @font-face {
      font-family: 'Shadows Into Light';
      font-style: normal;
      font-weight: 400;
      src: local('Shadows Into Light'), local('ShadowsIntoLight'), url(https://fonts.gstatic.com/s/shadowsintolight/v7/clhLqOv7MXn459PTh0gXYFK2TSYBz0eNcHnp4YqE4Ts.woff2) format('woff2');
      unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
    }

    .fixed_div{
        background: #BBF597; 
    } 
 
    .navbar{ 
        position:fixed;
        top:0;
        left:0;
        width:100%;
        height:3em;
        background:#BBF597;    
        z-index:9999; 

    }
 
    ul{
        margin:0 auto;
        padding:0;
        list-style-type:none;
        width:100%; 
        height:3em;
        float:left;
        display:block;
        box-sizing:border-box;
        z-index:9999;
        display:block;
        width:100%;     
   
    }
    li{
        float:left;
        width:14%;
        height:100%;
        
    }
    li a{
        color:white; 
        font-size:1.8em;
        margin:1em;
        font-weight:bold;
        text-decoration:none;        
    }
    li:hover{
        background:#FED966;
    }
    li a:hover{
        color:green;
    }
    .head_font{
        margin:3em;
    }
    .head_font_name{
        color: #0BCC47;
        font-size:6em;
        line-height:1em;

        margin-right:0.9em;
        text-transform:uppercase;   
    }
    .head_font_date{
        color: #0BCC47;
        font-size:3em;
        line-height:1em;
        margin-right:1.5em;
    }     
	.ishome li:nth-child(1){background-color:#FED966;}   
	.isabout li:nth-child(2){background-color:#FED966;}
	.istutorial li:nth-child(3){background-color:#FED966;}    
	.isstatistics li:nth-child(4){background-color:#FED966;}
	.isdownload li:nth-child(5){background-color:#FED966;}
	.isfaq li:nth-child(6){background-color:#FED966;}

    
</style>

<script>
    export default {
      name:'headernav'
    }
</script>


