<template>
    <div class="background">        
        <header-nav class="ishome"></header-nav>
        <three-select :all_data=l1_array v-on:listen_select_1_2_3="get_threeselect_array"></three-select> 
        <div id=get_fast_search_div><button id='get_fast_search_button' v-on:click='reverse_fast_search'>Fast Cell-line Search</button></div>
        <fast-search v-if='search_input' v-on:listen_fast_search='get_threeselect_array'></fast-search>
        <table-page v-if="test_threeselect()" :all_data=table_data v-on:listen_gsm='get_gsm_result_summary'></table-page> 
        <div>{{choose_gsm}}</div>
        <result-summary></result-summary>
        <Footer></Footer>            
    </div>
</template>

<script>
    import HeaderNav from '../components/headernav.vue'
    import Footer from '../components/footer.vue'
    import ThreeSelect from '../components/threeselect.vue'
    import FastSearch from '../components/fastsearch.vue'
    import TablePage from '../components/tablepage.vue'
    import ResultSummary from '../components/resultsummary.vue'
    import reqwest from 'reqwest'    
    
    export default{
        components: {
            HeaderNav,
            Footer,
            ThreeSelect,
            FastSearch,
            TablePage,
            ResultSummary
        },
        data(){
            return{
                isNowPage:true,
                l1_array:[],
                array_from_threeselect:[],
                search_input:false,
                choose_gsm:[]
            }
        },
        methods:{
            get_threeselect_array:function(data){
                this.array_from_threeselect=data;
            },
            get_gsm_result_summary:function(data){
                this.choose_gsm=data;
            },            
            test_threeselect(){
                if(
                    this.array_from_threeselect.length !=0
                    &&this.array_from_threeselect[0] !=''
                    &&this.array_from_threeselect[1]!=''
                    &&this.array_from_threeselect[2] !=''
                )
                {
                    return 1;
                }
                else
                {
                    return 0;
                }
            },
            reverse_fast_search(){
                this.search_input=! this.search_input
            }
        },
        computed:{
            table_data:function(){ 
                self=this;
                var l3_array=[]
                if(self.array_from_threeselect[2]=='*'){
                    for(var i in self.l1_array){
                        var obj1= self.l1_array[i];
                        console.log(obj1.specie_name,self.array_from_threeselect[0]);
                        if (obj1.specie_name==self.array_from_threeselect[0]){
                            var obj2=obj1.sources;
                            for(var j in obj2){
                                if (obj2[j].source_name==self.array_from_threeselect[1]){
                                    var obj3= obj2[j].process;
                                    for(var k in obj3){
                                        l3_array.push(obj3[k])
                                    }
                                }
                            }
                            break;
                        }
                        
                    }
                }                                   
                else{
                    for(var i in self.l1_array){
                        var obj1= self.l1_array[i];
                        if (obj1.specie_name==self.array_from_threeselect[0]){
                            var obj2=obj1.sources;
                            for(var j in obj2){
                                if (obj2[j].source_name==self.array_from_threeselect[1]){
                                    var obj3= obj2[j].process;
                                    for(var k in obj3){
                                        if (obj3[k].process_name==self.array_from_threeselect[2]){
                                            l3_array.push(obj3[k])
                                        }
                                    }
                                }
                            }
                            break;
                        }
                    }
                }    
                return l3_array;
            }
        },
        created:function(){
            var self=this;
            reqwest({
                url:'http://127.0.0.1:8000/relate_select_json/species_json/?format=json',
                type:'json',
                method:'get',
                success:function(response_data){
                    self.l1_array = response_data; 
                }
            });               
        }                           
    }
</script>

<style scoped>
    .background{
        background:url('../assets/background.jpg');
    }
    #get_fast_search_div{
        margin:1em 1em 1em 1em;
        width:100%;
        height:5em;
    }
    #get_fast_search_button{
        float:left;
        margin:0em 0 0 1em;
        font-size:1.5em;
        height:3em;
        width:10em;     
        text-align:left;
    }
</style> 


