<template>
    <div id='fast_search_app'>
        <div id='cell_search'>
            <div class='search_title'>Search by cell:</div>
            <div class='search_subtitle'>cell-line:</div>
            <input placeholder='e.g.MCF-7' v-model='message' class='search_input'></input> 
            <button v-on:click='search_cell' class='search_button'>search</button>
            <div class='search_result'>{{ output_message }}</div>
        </div>    
        
    </div>    
</template>
<script>
    export default {
        data(){
            return{
                message:'',
                output_message:'',
                cell_species_dict:{'MCF-7':'Homo sapiens','b':'mus'}
            }    
        },
        methods:{
            search_cell:function(){
                this.output_message='';
                if(this.message.toUpperCase() in this.cell_species_dict){
                    this.send_fast_search(); 
                    this.output_message='search result can be found below'
                }
                else{
                    this.output_message='cell-line not found';
                }
          },
            send_fast_search:function(){
                this.$emit(
                    "listen_fast_search",
                    [this.cell_species_dict[this.message.toUpperCase()],this.message.toUpperCase(),'*']
                )   
            }         
        }      
    }
</script>

<style scoped>
    #fast_search_app{
        margin:0;
        padding:0;
        border:0.9em solid #ADD1D6;
        margin-top: 2em;
        margin-left:3%;
        margin-right:3%;
        height:10em;
    }
    #cell_search{
        width:50%;
    }
    .search_title{
        color:light green;
        font-size:2em;
        float:left;
        position:relative;
        margin:1em 2em 0 2em;
        width:100%;
        text-align:left;        
    }
    .search_subtitle{
        color:light green;
        float:left;
        position:relative;
        margin:1em 1em 0 2.8em;
        font-size:1.5em;    
        border:black solid;
    } 
    .search_input{
        float:left;
        margin:1.5em 2em 0 1em;
        height:2em;
        border:black solid;
    }
    .search_button{
        height:2em;
        margin:1.5em 0 0 0;
        float:left;
    }
    
    .search_result{
        margin:0.8em 0 0 0;
        width:20em;
        color:black;
        font-size:1em;
        float:left;
    }
    
    
</style>








