<template>
    <table> 
        <thead>
            <tr>
                <th v-for='col in tabletitle'><div v-html='col'></div></th>
            </tr>
        </thead>  
        <tbody>
            <tr v-for='item in curList' class='tr_class' @click='send_gsm(item)'>
                <td v-for='col in gridColumns'>{{item[col]}}</td>
            </tr>
        </tbody>
        <tfoot>
            <td colspan="5">
                <div id='tfoot_div'>
                    <page-com :datanum="all_data.length" :each="eachPageSize" :visiblepage="visiblepage" :cur="cur" v-on:change-page="changePage"></page-com>
                </div>
            </td> 
        </tfoot>    
    </table> 
</template>
<script>
    import PageCom from './pagetable.vue'   
    export default{
        components: {
            PageCom
        },
        data(){
            return{
                gridColumns: ['gsm','srr','gse','pmid','process_name'],
                tabletitle:['GEO<br/>ID','SRA<br/>ID','Study<br/>ID','PMID','Treatments'],
                cur: 1, 
                eachPageSize: 3, 
                visiblepage: 5, 
                curpage: 1               
            } 
        },
        props:['all_data'],
        methods: {
            changePage: function (cur) {
                this.curpage = cur;
            },
            send_gsm:function(item_li){ 
                var gsm_id=item_li['gsm'];
                this.$emit(                    
                    "listen_gsm",
                    [gsm_id]
                )    
            }            
        },
        computed: {
            curList: function () {
                var p = [];
                for(var i = 0, l = this.all_data.length;i<l;i++){
                    p[i] = this.all_data[i];
                }
                if(p.length>3){
                    return p.splice((this.curpage-1) * this.eachPageSize, this.eachPageSize);
                }
                else{
                    return p
                }
                
            }
        }         
    }    
</script>
<style scoped>
    table{
        padding:0 0 0 0; 
        margin-left:3em;
        width: 90%;
        border:3px solid;
        margin-top:1em;
    }
    thead{  
        padding:0 0 0 0;
        margin:0 0 0 0;    
        height:3em;
        font-size:1.3em;
        width:100%;
        border-collapse:collapse;        
    } 
    th{
        padding:0 0 0 0;
        margin:0 0 0 0;     
        border:2px solid black;
        border-left:none;
        border-right:none;
        background-color:#B5F979;
        width:20%;
        
    } 
    tbody{        
        padding:0 0 0 0;
        margin:0 0 0 0;  
        width:100%;        
    }
    td{
        height:1.5em;
        border:2px solid black;
        border-left:none;
        border-right:none;

    }
    .tr_class{
        background-color:#ADD1D6;
    }
    .tr_class:hover{
        background-color:#9400D3;
    }
      
    #tfoot_div{
        height:2em;
        float:right;  
        background-color:#ADD1D6;
    }

   
</style>









