<template>
    <div class='bigframe'>
        <div class='updownframe'> 
        </div>
        <div class='titleframe'>
            <div class='titletext'>Species</div>
        </div>
        <div class='titleframe'>
            <div class='titletext'>Sources</div>
        </div>  
        <div class='titleframe'>
            <div class='titletext'>Treatment</div>
        </div>   
        <div class='dataframe'>
            <ul>
                <li v-for="item1 in sort_l1_array" @click="make_l1_inactive(),item_active(item1),change_select1(item1)"
                    v-bind:class="{'item-active': item1.is_active}">
                    <span>{{ item1.specie_name }}</span>
                </li>
            </ul>        
        </div>
        <div class='dataframe'>
            <ul v-model="get_l2_array">
                <li v-for="item2 in sort_l2_array" @click="make_l2_inactive(),item_active(item2),change_select2(item2)"
                    v-bind:class="{'item-active': item2.is_active}">
                    <span>{{item2.source_name}}</span>
                </li>
            </ul>
        </div>
        <div class='dataframe'>
            <ul v-model="get_l3_array">
                <li v-for="item3 in sort_l3_array" @click="make_l3_inactive(),item_active(item3),change_select3(item3),send_select_1_2_3()"
                    v-bind:class="{'item-active': item3.is_active}">
                    <span>{{item3.process_name}}</span>
                </li>
            </ul>        
        </div>  
        <div class='updownframe'>         
        </div> 
    </div>
</template>

<script>
    export default{
        data(){
            return{
                select1:{'Homo spacies':false},
                l2_array:[],
                select2:{'76NF2V':false},
                l3_array:[],
                select3:{},
                l1_list:[],
                l2_list:[],
                l3_list:[]
            }
        },
        props:['all_data'],
        methods:{
            make_l1_inactive: function(){
                for(var i=0; i<this.all_data.length;i++){
                    this.all_data[i].is_active = false;
                }
            },      
            item_active:function(item){    
                item.is_active = !item.is_active;         
            },
            change_select1:function(item){
                if(item.is_active==true){
                    this.select1={};
                    this.select1[item.specie_name]=true;
                }    
            },
            make_l2_inactive:function(){
                for(var i=0; i<this.l2_array.length;i++){
                    this.l2_array[i].is_active = false;
                }
            },
            change_select2:function(item){
                if(item.is_active==true){
                    this.select2={};
                    this.select2[item.source_name]=true;
                }    
            }, 
            make_l3_inactive:function(){
                for(var i=0; i<this.l3_array.length;i++){
                    this.l3_array[i].is_active = false;
                }
            },  
            change_select3:function(item){
                if(item.is_active==true){
                    this.select3={};
                    this.select3[item.process_name]=true;
                }    
            },
            send_select_1_2_3:function(){
                if(Object.values(this.select1)[0]&&Object.values(this.select2)[0]&&Object.values(this.select3)[0]){
                    this.$emit(
                        "listen_select_1_2_3",
                        [Object.keys(this.select1)[0],Object.keys(this.select2)[0],Object.keys(this.select3)[0]]
                    )
                }    
            },
            compare:function(propertyName){  
                return function (object1, object2) { 
                    var value1 = object1[propertyName].toUpperCase(); 
                    var value2 = object2[propertyName].toUpperCase(); 
                    if (value2 > value1) { 
                        return -1; 
                    } 
                    else if (value2 < value1) { 
                        return 1; 
                    } 
                    else { 
                        return 0; 
                    } 
                } 
            },                  
            cut_the_same:function(arr){
                var arr1 = [];
                var arr2 = [];
                for(var i=0;i<arr.length;i++){                    
                    if(!arr2.contains(arr[i].process_name)){
                        arr1.push(arr[i]);
                    };
                    arr2.push(arr[i].process_name);
                }
                return arr1;
            }
           
        },
        computed:{
            get_l2_array: function(){
                for(var i in this.all_data){
                    var obj= this.all_data[i];
                    if (obj.specie_name==Object.keys(this.select1)){
                        this.l2_array=obj.sources;
                        for(var j in this.l2_array){
                            var obj1= this.l2_array[j];
                            obj1.is_active=false;
                        }
                        break;
                    }
                }
            },
            get_l3_array:function(){
                for(var i in this.l2_array){
                    var obj= this.l2_array[i];
                    if (obj.source_name==Object.keys(this.select2)){
                        this.l3_array=obj.process;
                        for(var j in this.l3_array){
                            var obj1= this.l3_array[j];
                            obj1.is_active=false;
                        }                        
                        break;
                    }
                }           
            },
            sort_l1_array:function(){ 
                return this.all_data.sort(this.compare('specie_name')) 
            },
            sort_l2_array:function(){ 
                return this.l2_array.sort(this.compare('source_name')) 
            },
            sort_l3_array:function(){ 
                return this.cut_the_same(this.l3_array.sort(this.compare('process_name')))
            }            
            
        },
        created:function(){
            Array.prototype.contains = function (obj) {  
                var i = this.length;  
                while (i--) {  
                    if (this[i] === obj) {  
                        return true;  
                    }  
                }  
                return false;  
            }        
        }
    }    
</script>

<style scoped>
    .bigframe{
        margin:0;
        padding:0;
        border:0.9em solid #ADD1D6;
        margin-left:3%;
        margin-right:3%;
        height:30em;
    }
    .updownframe{
        border:0.3em solid #ADD1D6;
        margin:0;
        height:3em;
        float:left;
        width:100%;
        background:#B5F979;

    }
    .titleframe{
        width:30%;
        height:2.5em;
        border:0.3em solid #ADD1D6;
        float:left;
        margin-right:1%;
        margin-left:1%;
        text-align:center;
        font-weight:bold;                
    }
    .titletext{
        height:1.5em;
        width:100%;
        padding-top:0.5em;
    }
    .dataframe{
        width:25%;
        height:20em;
        border:0.3em solid #ADD1D6;
        float:left;
        margin-left:5%;
        margin-right:1%;
    }
    ul{
        padding:0; 
        margin:0;
        text-align:center;    
        list-style-type:none;
        display: block;
        background:#83B448;
        position:relative;
        overflow-y: auto;
        overflow-x: hidden;   
        min-height:20em;
        max-height:20em;
    } 
    li{
        padding-top:0.5em;
        font-weight:bold;
        color:white;
    }
    li:hover{
        height:1.5em;
        background:#AAD3D4;
    }  
    .item-active {
      background-color:#AAD3D4;
    }    
</style>


