<template>
    <ul>
          <li class='pre_next_li'><a v-on:click="prevClick()">Prev</a></li>
          <li v-for="index in pages" class='index_li'  v-bind:class="{ 'li-active': curPage == index}">
              <a v-on:click="btnClick(index)">{{ index }}</a>
          </li>
          <li class='pre_next_li'><a v-on:click="nextClick()">Next</a></li>
          <li class='index_summary_li' ><a><i>{{curPage}}/{{totalPages}}</i></a></li>
    </ul>
</template>

<style scoped>
    ul{
        padding:0 0 0 0;
        margin:0 0 0 0;
        text-align:center;    
        list-style-type:none;    
        display: block;
        width:100%;
        height:2em;
    }
    .index_li{
        width:2em;
        height:2em;
        float:left;  
        border:2px solid;
    }
    .index_li:hover{
        background-color:#9400D3;
    }
    .pre_next_li{
        width:5em;
        height:2em;
        float:left;     
        border:2px solid;        
    }
    .pre_next_li:hover{
        background-color:#9400D3;
    }    
    .index_summary_li{
        width:2em;
        height:2em;
        float:left;  
        border:2px solid;
    }    

/*    

    a{
        font-size:1em;
    }
*/    
</style>

<script>
    export default{
        data(){
            return {
                curPage:1
            }
        },    
        props:['datanum','each','visiblepage','cur'],
        created: function(){
            this.curPage = this.cur;
            
        },
        computed: {
            
            totalPages: function(){
                return Math.ceil(this.datanum / this.each) || 0;
            },
            pages: function(){
                var lowPage = 1;
                var highPage = this.totalPages;
                var pageArr = [];
                if(this.totalPages > this.visiblepage){
                    var sub = Math.ceil(this.visiblepage/2);
                    if(this.curPage > sub && this.curPage < this.totalPages - sub +1){
                        lowPage = this.curPage - sub;
                        highPage = this.curPage + sub - 2;
                    }
                    else if(this.curPage <= sub){
                        lowPage = 1;
                        highPage = this.visiblepage;
                    }
                    else{
                        lowPage = this.totalPages - this.visiblepage + 1;
                        highPage = this.totalPages;
                    }
                }
                while(lowPage <= highPage){
                    pageArr.push(lowPage);
                    lowPage ++;
                }
                return pageArr;
            }
        },
        methods: {
            btnClick: function(index){
                this.curPage = index;
                this.$emit('change-page', index)
            },
            nextClick: function(){
                if (this.curPage < this.totalPages) {
                    this.curPage++;
                    this.$emit('change-page', this.curPage)
                }
            },
            prevClick: function() {
                if (this.curPage > 1) {
                    this.curPage--;
                    this.$emit('change-page', this.curPage)
                }
            }
        }
    }    
</script>




